/* eslint-disable array-callback-return */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { React, useState } from 'react';
import { Typography, Tooltip, Space, Modal, Table, Select, DatePicker, Divider } from 'antd';

import moment from 'moment';


const { Text } = Typography;

const { Option } = Select;

export const StatisticModal = (props) => {
    const { visible, data, handleClose } = props;
    const [monthPicker, setMonthPicker] = useState(moment(new Date()));
    const [option, setOption] = useState('Month');
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    const [dataSource, setDatasource] = useState();
    const [businessDay, setBusinessDay] = useState(getBusinessDays(moment(new Date()).format("YYYY-MM-01"), moment(new Date()).format("YYYY-MM-") + moment(new Date()).daysInMonth()));
    const [workDays, setWorkDays] = useState();
    const [leaveNote, setLeaveNote] = useState();

    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            render: text => <a>{text}</a>,
        },
        {
            title: 'Month',
            dataIndex: 'month',
            key: 'month',
        },
        {
            title: 'M/d(s) for Projects',
            dataIndex: 'project',
            key: 'project',
        },
        {
            title: 'M/d (s) on research, support other teams',
            dataIndex: 'support',
            key: 'support',
            ellipsis: {
                showTitle: false,
            },
            render: support => (
                <a>{support}</a>
            ),
        },
        {
            title: 'M/d(s) for training',
            dataIndex: 'training',
            key: 'training',
        },
        {
            title: 'Leave',
            dataIndex: 'leave',
            key: 'leave',
        },
        {
            title: 'Total',
            dataIndex: 'total',
            key: 'total',
        }
    ];

    const columnsbyDate = [
        {
            title: 'Name',
            dataIndex: 'name',
            ellipsis: {
                showTitle: false,
            },
            width: 250,
            render: text => (
                <Tooltip placement="topLeft" title={text}>
                    {text}
                </Tooltip>
            ),
        },
        {
            title: 'Date',
            dataIndex: 'date',
            sorter: {
                compare: (a, b) => moment(a.date).format('X') - moment(b.date).format('X'),
                multiple: 5,
            },
        },
        {
            title: 'Projects',
            dataIndex: 'project',
            sorter: {
                compare: (a, b) => a.project.replace('h', '') - b.project.replace('h', ''),
                multiple: 4,
            },
        },
        {
            title: 'Research, support other teams',
            dataIndex: 'support',
            ellipsis: {
                showTitle: false,
            },
            render: support => (
                <a>{support}</a>
            ),
            sorter: {
                compare: (a, b) => a.support.replace('h', '') - b.support.replace('h', ''),
                multiple: 3,
            },
        },
        {
            title: 'Training',
            dataIndex: 'training',
            sorter: {
                compare: (a, b) => a.training.replace('h', '') - b.training.replace('h', ''),
                multiple: 2,
            },
        },
        {
            title: 'Leave',
            dataIndex: 'leave',
            sorter: {
                compare: (a, b) => a.leave.replace('h', '') - b.leave.replace('h', ''),
                multiple: 1,
            },
        },
        {
            title: 'Total',
            dataIndex: 'total',
        }
    ];

    let initDataSouce = [
        {
            key: '1',
            name: currentUser.name,
            month: moment(new Date()).format('MMMM-YYYY'),
            project: 0,
            support: 0,
            training: 0,
            leave: 0,
            total: 0
        }
    ];

    const initData = data.filter((item => (item.date ? moment(item.date).format('YYYYMM') : '') === moment(new Date()).format('YYYYMM')));
    let leaveDes = '';
    let workingDay = 0;
    // console.log('data', data);
    // console.log('initData', initData);
    initData.map(item => {
        let date =  moment(item.date).format('YYYY-MM-DD')
        if (item.data.count > 0) {
            let dataSource = item.data.dataSource;
            dataSource.map(rs => {
                switch (rs.Type) {
                    case "Project":
                        initDataSouce[0].project += rs.Hours / 8;
                        initDataSouce[0].total += rs.Hours / 8;
                        workingDay+= rs.Hours / 8;
                        break;
                    case "Support":
                        initDataSouce[0].support += rs.Hours / 8;
                        initDataSouce[0].total += rs.Hours / 8;
                        workingDay+= rs.Hours / 8;
                        break;
                    case "Training":
                        initDataSouce[0].training += rs.Hours / 8;
                        initDataSouce[0].total += rs.Hours / 8;
                        workingDay+= rs.Hours / 8;
                        break;
                    case "Leave":
                        initDataSouce[0].leave += rs.Hours / 8;
                        leaveDes += ' [' + date + ' ' + rs.Task + '] '
                        break;
                    default:
                        break;
                }
            });
        }
    });


    function getBusinessDays(startDate, endDate,) {
        var firstDay = moment(startDate);
        var lastDay = moment(endDate);

        let calcBusinessDays = 1 + (lastDay.diff(firstDay, 'days') * 5 -
            (firstDay.day() - lastDay.day()) * 2) / 7;

        if (lastDay.day() == 6) calcBusinessDays--;//SAT
        if (firstDay.day() == 0) calcBusinessDays--;//SUN

        return calcBusinessDays;
    };


    const getDatasourcebyMonth = (value) => {
        let source = [
            {
                key: '1',
                name: currentUser.name,
                month: moment(value).format('MMMM-YYYY'),
                //month: moment(value).format("YYYY-MM-01") + ' - ' + moment(value).format("YYYY-MM-") + moment(value).daysInMonth(),
                project: 0,
                support: 0,
                training: 0,
                leave: 0,
                total: 0
            }
        ];
        const valueData = data.filter((item => moment(item.date).format('YYYYMM') === moment(value).format('YYYYMM'))) || [];
        let calcBusinessDays = getBusinessDays(moment(value).format("YYYY-MM-01"), moment(value).format("YYYY-MM-") + moment(value).daysInMonth())
        setBusinessDay(calcBusinessDays);
        let leaveNote_ = '';

        valueData.map(item => {
            let count = item.data.count || 0;
            let date = moment(item.date).format('YYYY-MM-DD');
            if (count > 0) {
                let dataSource = item.data.dataSource;

                dataSource.map(rs => {
                    switch (rs.Type) {
                        case "Project":
                            source[0].project += rs.Hours / 8;
                            source[0].total += rs.Hours / 8;
                            break;
                        case "Support":
                            source[0].support += rs.Hours / 8;
                            source[0].total += rs.Hours / 8;
                            break;
                        case "Training":
                            source[0].training += rs.Hours / 8;
                            source[0].total += rs.Hours / 8;
                            break;
                        case "Leave":
                            source[0].leave += rs.Hours / 8;
                            calcBusinessDays -= rs.Hours / 8;
                            leaveNote_ += ' [' + date + ' ' + rs.Task + '] ';
                            break;
                        default:
                            break;
                    }
                });
            }
        });
        setLeaveNote(leaveNote_);
        setWorkDays(source[0].total);

        // source[0].total += "/" + calcBusinessDays + ' wds';
        return source;
    }

    const getDatasourcebyDate = (value) => {
        let source = [
        ];

        let valueData = data
            .filter((item => moment(item.date).format('YYYYMM') === moment(value).format('YYYYMM'))) || [];

        let calcBusinessDays = getBusinessDays(moment(value).format("YYYY-MM-01"), moment(value).format("YYYY-MM-") + moment(value).daysInMonth())
        setBusinessDay(calcBusinessDays);

        //console.log('valueDataSort', valueData);
        let key = 0;
        let workingDay = 0;

        valueData.map(item => {
            let count = item.data.count || 0;
            if (count > 0) {
                let dataSource = item.data.dataSource;
                key = key + 1;
                let row = {
                    key: key,
                    name: '',
                    date: moment(item.date || '19000101').format('YYYY-MM-DD'),
                    project: 0,
                    support: 0,
                    training: 0,
                    leave: 0,
                    total: 0
                };

                dataSource.map(rs => {
                    row.name += rs.Task + " ";
                    switch (rs.Type) {
                        case "Project":
                            row.project += rs.Hours;
                            row.total += rs.Hours;
                            break;
                        case "Support":
                            row.support += rs.Hours;
                            row.total += rs.Hours;
                            break;
                        case "Training":
                            row.training += rs.Hours;
                            row.total += rs.Hours;
                            break;
                        case "Leave":
                            row.leave += rs.Hours;
                            break;
                        default:
                            break;
                    }
                });
                row.project = row.project + 'h';
                row.support = row.support + 'h';
                row.training = row.training + 'h';
                row.leave = row.leave + 'h';
                workingDay += row.total;
                row.total = row.total + 'h';
                source.push(row);
                
            }
        });

        setWorkDays(workingDay / 8);
        return source;
    }

    const handleOk = e => {
        console.log(e);
        handleClose();
    };

    const handleCancel = e => {
        console.log(e);
        handleClose();
    };

    const onSelect = value => {
        // console.log('onselect', value, value.format('YYYYMM'));
        setMonthPicker(moment(value));
        setLeaveNote('');
        if (option === 'Month') {
            setDatasource(getDatasourcebyMonth(value));
        } else {
            setDatasource(getDatasourcebyDate(value));
        }
    };

    const handleSelectOption = (value) => {
        setOption(value);
        if (value === 'Month') {
            setDatasource(getDatasourcebyMonth(monthPicker));
        } else {
            setDatasource(getDatasourcebyDate(monthPicker));
        }
    }


    return (
        <Modal
            title="Manday report"
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            width={1000}
            footer={null}
        >
            <Space>
                <DatePicker picker="month" defaultValue={moment(new Date())} onSelect={onSelect} />
                <Select
                    defaultValue={option}
                    value={option}
                    onChange={handleSelectOption}
                >
                    <Option value="Month">Month</Option>
                    <Option value="Date">Date</Option>
                </Select>

            </Space>
            <hr />
            <Space split={<Divider type="vertical" />}>
                {/* <Text strong>User Name:</Text>
                <Text>{currentUser.name}</Text> */}
                 <Text >
                   <strong> From: </strong> {moment(monthPicker).format("YYYY-MM-01")} <strong>To:</strong> {moment(monthPicker).format("YYYY-MM-") + moment(monthPicker).daysInMonth()}
                </Text>

                <Text>
                <strong> Business day:</strong> {businessDay}
                </Text>
                <Text >
                <strong>Working day:</strong> {workDays === undefined ? workingDay : workDays}
                </Text>
                <Text> <strong>Report day:</strong> {moment(new Date()).format("YYYY-MM-DD")}</Text>
            </Space>


            {option === 'Month' ?
                <>
                    <Table columns={columns} dataSource={dataSource === undefined ? initDataSouce : dataSource} bordered />
                    <Text><strong>Note for Leave days:</strong> {leaveNote === undefined ? leaveDes : leaveNote }</Text>
                </>
                :
                <Table columns={columnsbyDate} dataSource={dataSource} bordered size="middle" />
            }
        </Modal>

    )
};

export default StatisticModal;