/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, useRef } from 'react';
import { Tag, Input } from 'antd';
import { PlusOutlined } from '@ant-design/icons';

const { CheckableTag } = Tag;

export const CheckTag = (props) => {
    const { selectedItems, setselectedItems } = props;
    const [selectedTags, setselectedTags] = useState([]);
    const [tagsData, settagsData] = useState(selectedItems);

    const [initTag, setInitTag] = useState({
        tags: tagsData || [],
        inputVisible: false,
        inputValue: '',
    });

    const { tags, inputVisible, inputValue } = initTag;
    const inputRef = useRef();

    useEffect(() => {
        if (inputRef.current && inputRef.current.focus) {
            inputRef.current.focus();
        }
    }, []);

    function handleChange(tag, checked) {
        const selectedTag = selectedTags;
        const nextSelectedTags = checked ? [...selectedTag, tag] : selectedTag.filter(t => t !== tag);
        setselectedTags(nextSelectedTags);
        setselectedItems(nextSelectedTags);
    }

    const handleClose = removedTag => {
        const tags = initTag.tags.filter(tag => tag !== removedTag);
        console.log(tags);
        setselectedItems(tags);

        setInitTag(prev => ({
            ...prev,
            tags: tags
        }));
    };

    const showInput = () => {
        setInitTag(prev => ({
            ...prev,
            inputVisible: true
        }));
    };

    const handleInputChange = e => {
        setInitTag(prev => ({
            ...prev,
            inputValue: e.target.value
        }));
    };

    const handleInputConfirm = () => {
        const { inputValue } = initTag;
        let { tags } = initTag;
        if (inputValue && tags.indexOf(inputValue) === -1) {
            tags = [...tags, inputValue];
        }
        console.log(tags);
        settagsData(tags);
        setInitTag(prev => ({
            ...prev,
            tags: tags,
            inputVisible: false,
            inputValue: '',
        }));
    };
    const forMap = tag => {
        const tagElem = (
            <Tag
                closable
                onClose={e => {
                    e.preventDefault();
                    handleClose(tag);
                }}
            >
                {tag}
            </Tag>
        );
        return (
            <span key={tag} style={{ display: 'inline-block' }}>
                {tagElem}
            </span>
        );
    };
    const tagChild = tags.map(forMap);
    return (
        <>
            <span style={{ marginRight: 8 }}><strong>Tags:</strong></span>
            {tagsData.map(tag => (
                <CheckableTag
                    key={tag}
                    checked={selectedTags.indexOf(tag) > -1}
                    onChange={checked => handleChange(tag, checked)}
                >
                    {tag}
                </CheckableTag>
            ))}
          
            {inputVisible && (
                <Input
                    ref={inputRef}
                    type="text"
                    size="small"
                    style={{ width: 200 }}
                    value={inputValue}
                    onChange={handleInputChange}
                    onBlur={handleInputConfirm}
                    onPressEnter={handleInputConfirm}
                />
            )}
            {!inputVisible && (
                <Tag onClick={showInput} className="site-tag-plus">
                    <PlusOutlined /> New Tag
                </Tag>
            )}
        </>
    );
}

export default CheckTag;