import React, { useEffect, useState } from "react";
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { notification, Alert, Button, Modal, Space, Typography, Progress, message, Avatar, Select } from 'antd';
import { Field, Formik, Form } from "formik";
import { object } from "yup";
import { AntInput, } from "../../../../components/Basic/AntFields";
import algoliasearch from 'algoliasearch';
import { Editor } from '../editor';
import { CheckTag } from '../CheckableTag';

const { Text, Title } = Typography;
const { Option } = Select;

export const ErrorMessageWrapperStyled = styled(Alert)`
    margin-bottom: 10px;

    .ant-alert-message {
        color: red;
    }
`;

export const ButtonSubmitWrapperStyled = styled(Button)`
    font-size: 16px;
    border-radius: 8px;
    background-color: #5468FF;
    border-color: #5468FF;
    color: #ffffff;
    justify-content: center;
    align-items: center;
`;

export const FormInputWrapperStyled = styled.div`
    .ant-form-item {
        margin-bottom: 5px;
    }
    .ant-form-inline {
        display: contents;
    }

    .ant-form-inline .ant-form-item {
        margin-right: 0px;
        margin-bottom: 0;
    }

    .ql-editor  {
        min-height: 200px;
    }

    .ant-tag {
        font-size: 14px;
    }

    .site-tag-plus {
        background: #fff;
        border-style: dashed;
        color: #5468FF;
    }
`

const LoginSchema = object({
    // catalog: string()
    //     .required(validationMessage.required),
    // schema: string()
    //     .required(validationMessage.required),
    // header: string()
    //     .required(validationMessage.required),
    // des: string()
    //     .required(validationMessage.required),
});


export const AddNewRecord = (props) => {

    const { visible, handleClose, onRefesh, profileObj } = props;
    const [percent, setPercent] = useState(0);
    const [visiblepercent, setVisiablePercent] = useState(false);

    const [selectedItems, setselectedItems] = useState(['Notes']);
    const OPTIONS = JSON.parse(localStorage.getItem("tags"));
    const [option, setOption] = useState(1);

    const [errorMessage, setErrorMessage] = useState("");
    const handleCloseErrorMessage = () => setErrorMessage("");
    const [notify, setNotify] = useState('');
    const [editContent, seteditContent] = useState({
        des: '',
        desHTML: '',
    });

    const client = algoliasearch('WLGAEUNVUN', 'b03c378d6e1fbbcb9e26865ce67143b1');
    const index = client.initIndex('tranthaitung.1989@gmail.com');

    const [initialValues, setInitialValues] = useState({
        tags: selectedItems,
        header: '',
        des: editContent.des,
        desHTML: editContent.desHTML,
        ref: '',
        creationDate: '',
        updateDate: '',
        email: profileObj ? profileObj.email : '',
        userName: profileObj ? profileObj.name : '',
        imageUrl: profileObj ? profileObj.imageUrl : '',
        bookmark: false,
        privacy: option
    });


    useEffect(() => {
        //console.log('selectedItems..', selectedItems);
    }, [selectedItems])


    useEffect(() => {
        if (percent >= 10 && percent <= 100) {
            // console.log('process', percent);
            setPercent(percent + 1);
        }
    }, [percent])


    const openNotification = placement => {
        notification['success']({
            message: `Notification add record`,
            description:
                `${notify} was added to search engine...`,
            placement,
        });
    };

    const handleSubmit = async (values, { setSubmitting, setStatus, resetForm }) => {
        setStatus({ error: null });
        setSubmitting(true);

        if (profileObj === null || profileObj.email === '') {
            message.error('Failed');
            return null;
        }

        let date = new Date();

        const records =
        {
            tags: selectedItems,
            header: values.header,
            des: editContent.des,
            desHTML: editContent.desHTML,
            ref: values.ref,
            creationDate: date,
            updateDate: date,
            bookmark: false,
            email: profileObj.email,
            userName: profileObj.name,
            imageUrl: profileObj.imageUrl,
            privacy: option
        };

        console.log('starting add', records);
        console.log('selectedItems', selectedItems);

        index.saveObject(records, { autoGenerateObjectIDIfNotExist: true })
            .then(({ objectIDs }) => {
                console.log(objectIDs);
                setVisiablePercent(true);
                setPercent(20);
                const timer = setTimeout(() => {
                    console.log('Will refresh Hit after 2 second!')
                    // setVisibleAdd(false);
                    setNotify(records.header);
                    setVisiablePercent(false);
                    onRefesh();
                    handleClose();
                }, 2000);
                return () => clearTimeout(timer);
            });
    };

    const handleSelectOption = (value) => {
        console.log('option', value);
        setOption(value);
    }

    useEffect(() => {
        if (notify.length > 0) {
            openNotification('topRight');
        }
    }, [notify]);

    return (
        <>
            <Formik
                initialValues={initialValues}
                validationSchema={LoginSchema}
                onSubmit={(values, formActions) => handleSubmit(values, formActions)
                }
            >
                {({ isSubmitting, resetForm }) => (
                    <Modal
                        centered
                        maskClosable={false}
                        visible={visible}
                        onCancel={() => {
                            resetForm();
                            handleClose();
                        }}
                        width={1000}
                        footer={null}
                    >
                        <FormInputWrapperStyled>
                            <Space size={1} direction="vertical" style={{ width: ' 100%' }}>
                                {/* <Title level={4}>Add new note</Title> */}

                                {visiblepercent && <Progress percent={percent} />}
                                <Form>
                                    {errorMessage && (
                                        <ErrorMessageWrapperStyled
                                            message={errorMessage}
                                            type="error"
                                            closable
                                            onClose={handleCloseErrorMessage}
                                        />
                                    )}
                                    <Space direction="vertical" style={{ marginLeft: 5 }} size={10}>
                                        <Space>
                                            <Avatar src={profileObj.imageUrl} />
                                            <CheckTag selectedItems={OPTIONS} setselectedItems={setselectedItems} />
                                        </Space>

                                        <Select
                                            defaultValue={option}
                                            value={option}
                                            onChange={handleSelectOption}
                                        >
                                            <Option value={1}><i class="fa fa-globe fa-sm" aria-hidden="true" /> Share to everyone </Option>
                                            <Option value={2}><i class="fa fa-lock fa-sm" aria-hidden="true" /> Only me</Option>
                                        </Select>
                                    </Space>
                                    <hr />
                                    {/* <Text><strong>Tags</strong></Text> */}
                                    {/* <EditableTagGroup selectedItems={selectedItems} setselectedItems={setselectedItems} /> */}
                                    {/* <SelectTagGroup options={OPTIONS} selectedItems={selectedItems} setselectedItems={setselectedItems} /> */}

                                    {/* <SelectOption
                                        options={optionsCatalog}
                                        defaultValue={catalog}
                                        setValueChange={setCatalog}
                                        placeholder={"Search to select Catalog"}
                                        label="Catalog"
                                        attribute="catalog" />

                                    <SelectOption
                                        options={optionsSchema}
                                        defaultValue={schema}
                                        setValueChange={setSchema}
                                        placeholder={"Search to select Schema"}
                                        label="Schema"
                                        attribute="schema" /> */}

                                    <Field
                                        placeholder="Please enter reference name/link"
                                        type="text"
                                        component={AntInput}
                                        name="ref"
                                        size="sm"
                                        label=""
                                        maxLength={1000}
                                        disabled={isSubmitting}
                                    />
                                    <Field
                                        placeholder="Please enter header"
                                        type="text"
                                        component={AntInput}
                                        name="header"
                                        size="sm"
                                        label=""
                                        maxLength={1000}
                                        disabled={isSubmitting}
                                    />

                                    <Editor defaultValue={editContent} setValues={seteditContent} />

                                    <ButtonSubmitWrapperStyled
                                        block
                                        htmlType="submit"
                                        size="large"
                                    >
                                        Add
                                    </ButtonSubmitWrapperStyled>
                                </Form>
                            </Space>
                        </FormInputWrapperStyled>
                    </Modal>
                )}
            </Formik>
        </>

    );
};

AddNewRecord.propTypes = {
    visible: PropTypes.bool.isRequired,
    handleClose: PropTypes.func.isRequired,
};

export default AddNewRecord;
