const updatePrivacy = (records) => {
  const index = adminClient.initIndex('tranthaitung.1989@gmail.com');
  const currentUser = JSON.parse(localStorage.getItem('currentUser'));

  const recordShare = records;
  recordShare.userName = currentUser.name;
  recordShare.imageUrl = currentUser.imageUrl;
  recordShare.email = currentUser.email;

  index
    .saveObject(records, { autoGenerateObjectIDIfNotExist: true })
    .then(({ objectIDs }) => {
      message.success('Save');
    });
}

const Hit = props => {
  const currentUser = JSON.parse(localStorage.getItem('currentUser'));

  const [enableEdit, setEnableEdit] = useState(false);
  const [activeLink, setActiveLink] = useState(true);
  const [checked, setChecked] = useState(false);
  const [initialValues, setInitialValues] = useState({
    objectID: props.hit.objectID,
    tags: props.hit.tags,
    header: props.hit.header,
    des: props.hit.des,
    desHTML: props.hit.desHTML,
    ref: props.hit.ref,
    creationDate: props.hit.creationDate,
    updateDate: props.hit.updateDate,
    bookmark: props.hit.bookmark === 'undefined' ? false : props.hit.bookmark,
    privacy: props.hit.privacy === undefined ? 2 : props.hit.privacy,
    email: props.hit.email,
    userName: props.hit.userName || '',
    imageUrl: props.hit.imageUrl || '',
  });

  const [updateDate, setupdateDate] = useState(props.hit.updateDate);
  const [header, setheader] = useState(props.hit.header);
  const [ref, setRef] = useState(props.hit.ref);
  const [editContent, seteditContent] = useState({
    des: props.hit.des,
    desHTML: props.hit.desHTML,
  });
  const [visiblePrivacy, setVisiblePrivacy] = useState(false);
  const [privacyValue, setPrivacyValue] = useState(props.hit.privacy === undefined ? 2 : props.hit.privacy);
  const [bookmark, setBookmark] = useState(props.hit.bookmark === 'undefined' ? false : props.hit.bookmark);

  const readOnly = props.hit.email === currentUser.email;


  const [selectedItems, setselectedItems] = useState(props.hit.tags);
  const ellipsis = true;
  const [loading, setLoading] = useState(false);

  const showModalPrivacy = () => {
    if (enableEdit) {
      message.error("You must save your note first...")
    } else {
      setVisiblePrivacy(true);
    }
  };

  const handleSaveNote = () => {
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    const index = adminClient.initIndex('tranthaitung.1989@gmail.com');

    if (currentUser.email === '') {
      message.error('You need to sign in or sign up before continuing');
      return null;
    }


    setLoading(true);

    let newDate = new Date();

    const records =
    {
      objectID: props.hit.objectID,
      tags: selectedItems,
      header: header,
      des: editContent.des,
      desHTML: editContent.desHTML,
      ref: ref,
      creationDate: props.hit.creationDate,
      updateDate: newDate,
      bookmark: bookmark === undefined ? false : bookmark,
      email: currentUser.email,
      userName: currentUser.name,
      imageUrl: currentUser.imageUrl,
      privacy: privacyValue,
    };


    index
      .saveObject(records, { autoGenerateObjectIDIfNotExist: true })
      .then(({ objectIDs }) => {
        console.log('saved', records);
        setheader(header);
        setBookmark(bookmark);
        seteditContent({
          des: records.des,
          desHTML: records.desHTML,
        });
        setRef(ref);
        setupdateDate(newDate);
        setEnableEdit(false);
        message.success('Updated success.');
        setLoading(false);
      });
  }

  useEffect(() => {
    if (header === initialValues.header && ref === initialValues.ref && bookmark === initialValues.bookmark) {
      setEnableEdit(false);
    } else {
      setEnableEdit(true);
    }
  }, [header, initialValues.header, initialValues.ref, ref, bookmark, initialValues.bookmark]);


  const handleDeleteChange = (e) => {
    let deletelist = localStorage.getItem("deletelist");

    if (e.target.checked) {
      deletelist += deletelist === "" ? props.hit.objectID : "," + props.hit.objectID;
    } else {
      if (deletelist.includes(",")) {
        deletelist = deletelist.replace("," + props.hit.objectID, "");
      } else {
        deletelist = deletelist.replace(props.hit.objectID, "");
      }
    }

    localStorage.setItem('deletelist', deletelist);
    console.log('deletelist', localStorage.getItem("deletelist"));
    setChecked(e.target.checked);
  }

  const onclick = (e) => { }

  const handleBookmark = (e) => {
    setBookmark(!bookmark);
  }

  const handleClosePrivacyModel = () => {
    setVisiblePrivacy(false);
  }

  useEffect(() => {
    if (privacyValue !== initialValues.privacy) {
      const record = initialValues;
      record.privacy = privacyValue;
      updatePrivacy(record);
      console.log('privacyValue change', privacyValue);
    }
  }, [privacyValue, initialValues]);

  return (
    <Spin spinning={loading}>
      {readOnly ?
        <ListItem>
          <ContainerStyles>
            <Descriptions
              column={1}
              title={
                <>
                  <Avatar src={props.hit.imageUrl} />
                  <Space direction="vertical" style={{ marginLeft: 5 }} size={-5}>
                    <Text strong>{props.hit.userName}</Text>
                    <Space>
                      <PostDateStyles>
                        {props.hit.updateDate ? <ReactTimeAgo date={props.hit.updateDate} locale="en-US" /> : null}
                      </PostDateStyles>
                      <Button type="link" onClick={showModalPrivacy}
                        style={{ color: '#333333' }}
                      >
                        {privacyValue === 1 ? <i class="fa fa-globe fa-sm" aria-hidden="true" /> : <i class="fa fa-lock fa-sm" aria-hidden="true" />}
                      </Button>
                      <PrivacyModal visible={visiblePrivacy} value={privacyValue} setValue={setPrivacyValue} handleClose={handleClosePrivacyModel} />
                    </Space>
                  </Space>
                  <br />
                  <Space>
                    <InputHeaderRefStyle>
                      {ref === null || ref === "" || ref === undefined
                        ? <Text editable={{ onChange: setRef, tooltip: 'click to add ref name/link' }}
                          onClick={onclick} style={ellipsis ? { width: 250 } : undefined}
                          ellipsis={ellipsis ? { tooltip: 'I am ellipsis now!', } : false}
                        >
                          {ref}
                        </Text>
                        : (activeLink && (ref.startsWith("https://") || ref.startsWith("http://"))
                          ?
                          <Link href={ref} target="_blank">
                            <Text
                              copyable
                              editable={{ onChange: setRef, tooltip: 'click to edit ref name/link' }} onClick={onclick}
                              style={ellipsis ? { width: 250, } : undefined}
                              ellipsis={ellipsis ? {} : false}
                            >
                              {ref}
                            </Text>
                          </Link>
                          :
                          <Text copyable editable={{ onChange: setRef, tooltip: 'click to edit ref name/link' }}
                            style={ellipsis ? { width: 250, } : undefined}
                            ellipsis={ellipsis ? {} : false}
                            onClick={onclick}>{ref}
                          </Text>
                        )
                      }
                    </InputHeaderRefStyle>
                  </Space>

                  <InputHeadertyle editable={{ onChange: setheader, tooltip: 'click to edit header', }}>
                    {header}
                  </InputHeadertyle>
                </>
              }
              extra={<>
                <Space size={5}>
                  <Tooltip placement="topRight" title='Edit Note'>
                    {enableEdit ?
                      <ButtonEditNote shape="circle" type="link" onClick={handleSaveNote} icon={<SyncOutlined spin />} /> : null}
                  </Tooltip>

                  <Tooltip placement="topRight" title={bookmark ? 'Remove flag item' : "Set flag note"}>
                    <Button shape="circle" type="link" onClick={handleBookmark}>
                      {bookmark === true ? <FlagFilled style={{ fontSize: 24, color: '#D32F2F' }} /> :
                        <FlagTwoTone style={{ fontSize: 24 }} />}
                    </Button>
                  </Tooltip>

                  {bookmark ? null : <CustomCheckBox className={checked ? "button-del-inner-checked" : "button-del-inner"}
                    checked={checked}
                    onChange={handleDeleteChange}
                  >   <Tooltip placement="bottomLeft" title='Select to delete note'>
                      <DelIconStyle><i class="fa fa-trash fa-lg" aria-hidden="true"></i></DelIconStyle>
                    </Tooltip>
                  </CustomCheckBox>}
                </Space>
              </>}
            >

              <Descriptions.Item>
                <Editor defaultValue={editContent} setValues={seteditContent} setEnableEdit={setEnableEdit} readOnly={false} />
              </Descriptions.Item>

              <Descriptions.Item>
                <EditableTagGroup selectedItems={selectedItems} setselectedItems={setselectedItems} setEnableEdit={setEnableEdit} />
              </Descriptions.Item>

            </Descriptions>
          </ContainerStyles>
        </ListItem>
        :
        <ListItem>
          <ContainerStyles>
            <Descriptions
              column={1}
              title={
                <>
                  <Avatar src={props.hit.imageUrl} />
                  <Space direction="vertical" style={{ marginLeft: 5 }} size={-5}>
                    <Text strong>{props.hit.userName}</Text>
                    <Space>
                      <PostDateStyles>
                        {props.hit.updateDate ? <ReactTimeAgo date={props.hit.updateDate} locale="en-US" /> : null}
                      </PostDateStyles>
                      <Button type="link" style={{ color: '#333333' }}><i class="fa fa-globe fa-sm" aria-hidden="true" /> </Button>
                    </Space>
                  </Space>
                  <InputHeaderRefStyle>
                    {ref === null || ref === "" || ref === undefined ? null
                      : (activeLink && (ref.startsWith("https://") || ref.startsWith("http://"))
                        ?
                        <Link href={ref} target="_blank">
                          <Text copyable style={ellipsis ? { width: 250 } : undefined}
                            ellipsis={ellipsis ? {} : false}>{ref} </Text>
                        </Link>
                        :
                        <Text copyable style={ellipsis ? { width: 250 } : undefined}
                          ellipsis={ellipsis ? {} : false}
                          onClick={onclick}>{ref}
                        </Text>
                      )
                    }
                  </InputHeaderRefStyle>
                  <InputHeadertyle>{header}</InputHeadertyle>
                </>
              }
              extra={<>
                <Space size={5}>

                  <Button shape="circle" type="link">
                    {bookmark === true ? <FlagFilled style={{ fontSize: 24, color: '#D32F2F' }} /> :
                      <FlagTwoTone style={{ fontSize: 24 }} />}
                  </Button>

                </Space>
              </>}
            >
              <Descriptions.Item>
                <Editor defaultValue={editContent} setValues={seteditContent} setEnableEdit={setEnableEdit} readOnly={true} showToolbar={false} />
              </Descriptions.Item>
              <Descriptions.Item>
                <TagGroup selectedItems={selectedItems} />
              </Descriptions.Item>
            </Descriptions>
          </ContainerStyles>
        </ListItem>
      }
      <hr />
    </Spin>
  );
};


