const Results = (props) => {
  const { onRefesh, indexName, profile } = props;
  const [percent, setPercent] = useState(0);
  const [visiblepercent, setVisiablePercent] = useState(false);

  useEffect(() => {
    if (percent >= 10 && percent <= 100) {
      setPercent(percent + 1);
    }
  }, [percent])

  localStorage.setItem('deletelist', "");

  const confirm = () => {
    const deletelist = localStorage.getItem('deletelist');
    if (deletelist === "") {
      message.error('Please select an item');
      return null;
    };

    const list = deletelist.split(',');
    const index = adminClient.initIndex(indexName);

    index.deleteObjects(list).then(({ objectIDs }) => {
      setVisiablePercent(true);
      setPercent(10);

      localStorage.setItem('deletelist', "");

      const timer = setTimeout(() => {
        onRefesh();
        setVisiablePercent(false);
        message.success('Delete sucess.');
      }, 2000);
      return () => clearTimeout(timer);
    });
  }

  return (
    <article>
      {/* <p className="sortby"> Sort by</p>
      <Space size="large" style={{ width: '100%', justifyContent: 'space-between', paddingLeft: '50px' }}>
        <SortByStyle>
          <SortBy
            items={[
              { value: 'instant_search', label: 'Date' },
              { value: 'instant_search_tags', label: 'Tags' },
            ]}
            defaultRefinement="instant_search"
          />
        </SortByStyle>
        <Tooltip placement="leftBottom" title='Add new record'>
          {visibleAdd && <AddNewRecord visible={visibleAdd} handleClose={onCloseAdd} />}
          <Button type="primary" icon={<AppstoreAddOutlined />} onClick={onOpenAdd}>Add Record</Button>
        </Tooltip>
      </Space> */}
      <div id="stats" className="text-right text-muted textResult">
        <Space>
          <Stats />
          {profile === null || profile === undefined ? null :
            <Popconfirm
              title="Are you sure to delete this record?
                 The action can not be undo!!!"
              onConfirm={confirm}
              okText="Yes"
              cancelText="No"
            >
              <ButtonDelStyle shape="circle" type="default" >
                <img className="delLogo" src={delLogo} alt="logo" />
              </ButtonDelStyle>
            </Popconfirm>
          }
        </Space>
      </div>

      {visiblepercent && <Progress percent={percent} />}
      <>
        <div id="hits">
          <Hits hitComponent={Hit} />
        </div>
        <div id="pagination" className="text-center">
          <Pagination />
        </div>
        <BackTop>
          <BackTopstyle><i class="fa fa-arrow-up"></i></BackTopstyle>
        </BackTop>
      </>
      <CustomResults />
    </article>
  );
};

const CustomResultsManday = connectStateResults(({ searchState, searchResults }) => {
  const currentUser = JSON.parse(localStorage.getItem('currentUser'));
  const data = searchResults && searchResults.nbHits ? searchResults.hits : [];
  const userData = data.filter(item => (item.email === currentUser.email));
  const loading = userData.length > 0 && userData[0].data === undefined;
  console.log(userData, userData.length === 0, userData.length > 0 && userData[0].data === undefined, loading);

  return (
    <> {userData.length === 0 ?
      <RenderCalender data={userData} /> :
      <Spin spinning={loading}>
        {loading ? <Empty /> : <RenderCalender data={userData} />}
      </Spin>}
    </>

  );
});

const RenderCalender = (props) => {
  const index = adminClient.initIndex('prod_MANDAY');
  const currentUser = JSON.parse(localStorage.getItem('currentUser'));
  const { data } = props;
  const [value, setValue] = useState(moment(new Date()));
  const [visibleStatistic, setvisibleStatistic] = useState(false);
  const [disabledDel, setDisabledDel] = useState(false);
  const [objectID, setObjectID] = useState('');
  const [selectedValue, setSelectedValue] = useState(moment(new Date()));
  const [selectDateData, setSelectDateData] = useState();

  const userData = data.filter((item => (moment(item.date).format('YYYY') === value.format('YYYY'))));
  const initData = userData.filter((item => (moment(item.date).format('YYYYMMDD') === value.format('YYYYMMDD'))))

  let initselectDateData = { dataSource: [], count: 0 };

  if (initData.length > 0) {
    initselectDateData = initData[0].data;
    console.log(userData, initselectDateData, selectDateData);
  }

  const onSelect = value => {
    setSelectDateData({
      dataSource: [],
      count: 0,
    });

    setValue(value);
    setSelectedValue(value);
    setDisabledDel(false);
    setObjectID('');

    userData.map(item => {
      if (item.email === currentUser.email && moment(item.date).format('YYYYMMDD') === value.format('YYYYMMDD')) {
        setSelectDateData(item.data);
        setObjectID(item.objectID);
        setDisabledDel(true);
        return '';
      }
    })
  };

  const onPanelChange = value => {
    setValue(value);
  };

  const showModal = () => {
    setvisibleStatistic(true);
  };

  const handleCloseModal = () => {
    setvisibleStatistic(false);
  }

  function getListData(value) {
    let listData;
    userData.map(item => {
      if (moment(item.date).format('YYYYMMDD') === value.format('YYYYMMDD')) {

        let typeColor;
        let data = item.data || {
          dataSource: [],
          count: 0,
        };
        if (data.count > 0) {
          let hours = 0;
          let dataSource = data.dataSource;
          let leave = 0;

          dataSource.map(hr => {
            hours += hr.Hours;
            if (hr.Type === 'Leave'){
              leave+=hr.Hours;
            }
            return hours;
          });

          if (hours === 8) {
            typeColor = 'success';
          } else {
            typeColor = 'warning';
          }

          if (leave > 0){
            typeColor = 'error';
          }

          listData = [
            {
              type: typeColor,
              content: 'Logged: ' + hours + 'h',
            }
          ];
          //console.log('listData', listData);
        }
      }
      return listData;
    })

    return listData || [];
  }

  useEffect(() => {
    //console.log('selectDateData', selectDateData);
  }, [selectDateData])

  function dateCellRender(value) {
    const listData = getListData(value);
    return (
      <ul className="events">
        {listData.map(item => (
          <li key={item.content}>
            <Badge status={item.type} text={item.content} />
          </li>
        ))}
      </ul>
    );
  }

  //======================================= get initialValues

  //======================================================

  const handleSaveTask = () => {
    if (currentUser.email === '') {
      message.warning("You need to login to use this function");
      return null;
    }

    if (selectDateData.count === 0) {
      message.error('Missing insert data');
      return null;
    }
    let record = {};

    if (objectID === '') {
      record =
      {
        userName: currentUser.name,
        email: currentUser.email,
        data: selectDateData,
        date: selectedValue,
        creationDate: new Date()
      };
    } else {
      record =
      {
        objectID: objectID,
        userName: currentUser.name,
        email: currentUser.email,
        data: selectDateData,
        date: selectedValue,
        creationDate: new Date()
      };
    }


    index.saveObject(record, { autoGenerateObjectIDIfNotExist: true })
      .then(({ objectID }) => {
        setObjectID(objectID)
        message.success('Saved. Please click button refresh to reload data.');
      });
  }

  const handleRemove = () => {
    if (objectID === '') {
      return;
    }
    index.deleteObject(objectID).then(({ objectID }) => {
      setObjectID('');
      message.success('Deleted');
    });
  }


  return (
    <>
      <Divider orientation="center">{`You selected date: ${selectedValue && selectedValue.format('YYYY-MM-DD')}`}</Divider>
      <div style={{ float: 'right' }}>
        <Space wrap>
          {selectDateData !== undefined ?
            <>
              {
                selectDateData.count > 0 ?
                  <Button type="primary" icon={<AppstoreAddOutlined />} onClick={handleSaveTask}>Save</Button>
                  :
                  <Button type="primary" icon={<AppstoreAddOutlined />} disabled onClick={handleSaveTask}>Save</Button>
              }

              {disabledDel === false ?
                <Button type="primary" icon={<DeleteOutlined />} disabled>Remove</Button>
                :
                <Popconfirm
                  title="Sure to delete?"
                  onConfirm={handleRemove}
                  okText="Yes"
                  cancelText="No"
                >
                  <Button type="primary" icon={<DeleteOutlined />}>Remove</Button>
                </Popconfirm>
              }
            </>
            :
            <>
              {initselectDateData.count > 0 ?
                <Button type="primary" icon={<AppstoreAddOutlined />} onClick={handleSaveTask}>Save</Button>
                :
                <Button type="primary" icon={<AppstoreAddOutlined />} disabled onClick={handleSaveTask}>Save</Button>}

              {initselectDateData.count === 0 ?
                <Button type="primary" icon={<DeleteOutlined />} disabled>Remove</Button>
                :
                <Popconfirm
                  title="Sure to delete?"
                  onConfirm={handleRemove}
                  okText="Yes"
                  cancelText="No"
                >
                  <Button type="primary" icon={<DeleteOutlined />}>Remove</Button>
                </Popconfirm>
              }
            </>
          }

          <Button type="primary" icon={<PieChartFilled />} onClick={showModal}>Statistic</Button>
          <StatisticModal visible={visibleStatistic} data={userData} handleClose={handleCloseModal} />

        </Space>
      </div>

      <EditTable data={selectDateData !== undefined ? selectDateData : initselectDateData} setData={setSelectDateData} />

      <CalendarStyles>
        <Calendar
          dateCellRender={dateCellRender}
          value={value}
          onSelect={onSelect}
          onPanelChange={onPanelChange} />
      </CalendarStyles>
    </>
  );
}

