import React from "react";
import { Form } from "antd";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import PropTypes from 'prop-types';
import styled from 'styled-components';

const atValues = [
    { id: 1, value: "Fredrik Sundqvist" },
    { id: 2, value: "Patrik Sjölin" }
];


const CustomEditor = styled.div`
   
    .ql-container {
        font-family: SFUIText,Helvetica Neue,sans-serif;
        font-size: 15px;
    }

    .ql-snow .ql-picker.ql-header {
        width: 80px;
    }

    .ql-toolbar.ql-snow {
        border: none;
        padding: 0px;
        opacity: 30%;
        margin-left: 20px;
        display: ${(props) => (props.showToolbar ? 'block' : 'none')}
    }
    .ql-container.ql-snow {
        border: none;
    }

    .ql-snow .ql-editor img {
        max-width: 200px;
    }

    .ql-snow .ql-editor pre.ql-syntax {
        background-color: #f0f0f0;
        color: #304FFE;
    }

    &:hover {
        .ql-toolbar.ql-snow {
            opacity: 1;
        }
      }
`

CustomEditor.propTypes = {
    showToolbar: PropTypes.bool,
};
CustomEditor.defaultProps = {
    showToolbar: true,
};

export const Editor = (props) => {
    const { defaultValue, setValues, setEnableEdit, readOnly, showToolbar } = props;

    const quillToolbar = {
        toolbar: [
            // [{ header: [2, 3, false] }],
            ["bold", "italic", "underline"], //"bold", "italic", "underline", "strike", "blockquote"
            //[{ list: "ordered" }, { list: "bullet" }],
            [{ 'list': 'ordered' }, { 'list': 'bullet' }, 
            { 'color': ['#000000', '#e60000', '#ff9900', '#ffff00', '#008a00', '#0066cc', '#9933ff', '#ffffff', '#facccc', '#ffebcc', '#ffffcc', '#cce8cc', '#cce0f5', '#ebd6ff', '#bbbbbb', '#f06666', '#ffc266', '#ffff66', '#66b966', '#66a3e0', '#c285ff', '#888888', '#a10000', '#b26b00', '#b2b200', '#006100', '#0047b2', '#6b24b2', '#444444', '#5c0000', '#663d00', '#666600', '#003700', '#002966', '#3d1466', 'custom-color'] }, { 'background': [] }, 'link'],

            // ["image"]
        ],
    };

    const modules = {
        toolbar: [
            //   [{ header: [2, 3, false] }],
            ["bold", "italic", "blockquote"], //"bold", "italic", "underline", "strike", "blockquote"
            [{ list: "ordered" }, { list: "bullet" }],
            //   ["link", "image"]
        ],
        mention: {
            allowedChars: /^[A-Za-z\sÅÄÖåäö]*$/,
            mentionDenotationChars: ["@", "#"],
            source: function (searchTerm, renderItem, mentionChar) {
                let values;
                if (mentionChar === "@" || mentionChar === "#") {
                    values = atValues;
                }
                if (searchTerm.length === 0) {
                    renderItem(values, searchTerm);
                } else {
                    const matches = [];
                    for (let i = 0; i < values.length; i++)
                        if (
                            ~values[i].value.toLowerCase().indexOf(searchTerm.toLowerCase())
                        )
                            matches.push(values[i]);
                    renderItem(matches, searchTerm);
                }
            }
        }
    };

    const handleProcedureContentChange = (content, delta, source, editor) => {
        const des = {
            des: editor.getText(),
            desHTML: editor.getHTML(),
        }
      
        setValues(des);
        if (setEnableEdit){
            setEnableEdit(true);
        } 
    };

    return (
        <>
            <Form>
                <Form.Item>
                    <CustomEditor showToolbar={showToolbar}>
                        <ReactQuill
                            modules={quillToolbar}
                            defaultValue={defaultValue.desHTML}
                            onChange={handleProcedureContentChange}
                            readOnly = {readOnly}
                        />
                    </CustomEditor>
                </Form.Item>
            </Form>
        </>
    );
}

Editor.propTypes = {
    defaultValue: PropTypes.any,
    setValues: PropTypes.func,
    setEnableEdit: PropTypes.func,
};

Editor.defaultProps = {
    setEnableEdit: false
};

export default Editor;
