/**
 *
 * AntFields Component
 *
 */
/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable react/prop-types */
import React from "react";
import {
  DatePicker,
  Input,
  TimePicker,
  Select,
  Radio,
  Checkbox,
  InputNumber,
} from "antd";
import CountryPhoneInput, { ConfigProvider } from "antd-country-phone-input";
import en from "world_countries_lists/data/en/world.json";
import { AntFieldStyle, RadioGroup, CheckboxGroup } from "./style";
import moment from 'moment'
const { Option } = Select;
const { TextArea } = Input;


const AntField = (AntComponent) => ({
  field,
  form,
  hasFeedback,
  label,
  selectOptions,
  submitCount,
  type,
  required,
  setFormatedDate,
  selectedPlan,
  setSelectedPlan,
  ...props
}) => {
  const touched = form.touched[field.name];
  const submitted = submitCount > 0;
  const hasError = form.errors[field.name];
  const submittedError = hasError && submitted;
  const touchedError = hasError && touched;

  const onInputChange = ({ target: { value } }) => {
    if(selectedPlan!==''&& selectedPlan!==undefined  ){
      form.setFieldValue('plan', selectedPlan);
      setSelectedPlan('')
    }
    form.setFieldValue(field.name, value);
    // eslint-disable-next-line no-unused-expressions
    props.onChange && props.onChange(value);
  };
  const onDateTimeChange = (value, dateString) => {
   let  getMilliseconds=value?._d.getMilliseconds()
    form.setFieldValue(field.name, value);
    setFormatedDate(Number(moment(value?._d).unix()+getMilliseconds.toString()));
  };
  const onChange = (value) => {
    form.setFieldValue(field.name, value);
    // eslint-disable-next-line no-unused-expressions
    props.onChange && props.onChange(value);
  };

  const onBlur = () => form.setFieldTouched(field.name, true);
  return (
    <AntFieldStyle
      label={label}
      help={submittedError || touchedError ? hasError : false}
      validateStatus={submittedError || touchedError ? "error" : "success"}
      required={required}
      labelCol={{
        xs: { span: 24 },
        md: { span: 24 },
        lg: { span: props.labelCol || 24 },
      }}
      wrapperCol={{ md: { span: 24 }, lg: { span: props.wrapperCol || 24 } }}
      colon={false}
      labelAlign={props.labelAlign || "left"}
    >
      <AntComponent
        {...field}
        {...props}
        onBlur={onBlur}
        onChange={
          type === "select" || type === "inputNumber"
            ? onChange
            : type === "dateTime"
            ? onDateTimeChange
            : type === "tel"
            ? onChange
            : onInputChange
        }
        type={type}
      >
        {selectOptions &&
          selectOptions.map((option) => (
            <Option key={option.value}>{option.name}</Option>
          ))}
      </AntComponent>
    </AntFieldStyle>
  );
};

export const RadioField = ({
  field,
  form,
  hasFeedback,
  label,
  options,
  submitCount,
  required,
  vertical,
  ...props
}) => {
  const touched = form.touched[field.name];
  const submitted = submitCount > 0;
  const hasError = form.errors[field.name];
  const submittedError = hasError && submitted;
  const touchedError = hasError && touched;
  return (
    <AntFieldStyle
      label={label}
      help={submittedError || touchedError ? hasError : false}
      validateStatus={submittedError || touchedError ? "error" : "success"}
      required={required}
      labelCol={{ md: { span: 24 }, lg: { span: props.labelCol || 24 } }}
      wrapperCol={{ md: { span: 24 }, lg: { span: props.wrapperCol || 24 } }}
      colon={false}
      labelAlign={props.labelAlign || "left"}
    >
      <RadioGroup vertical={vertical} {...field} {...props}>
        {options.map((option) => (
          <Radio key={option.value} value={option.value}>
            {option.name}
          </Radio>
        ))}
      </RadioGroup>
    </AntFieldStyle>
  );
};

export const CheckboxField = ({
  field,
  form,
  hasFeedback,
  label,
  options,
  submitCount,
  required,
  ...props
}) => {
  const touched = form.touched[field.name];
  const submitted = submitCount > 0;
  const hasError = form.errors[field.name];
  const submittedError = hasError && submitted;
  const touchedError = hasError && touched;
  const onChange = (value) => form.setFieldValue(field.name, value);
  return (
    <AntFieldStyle
      label={label}
      help={submittedError || touchedError ? hasError : false}
      validateStatus={submittedError || touchedError ? "error" : "success"}
      required={required}
      labelCol={{ md: { span: 24 }, lg: { span: props.labelCol || 24 } }}
      wrapperCol={{ md: { span: 24 }, lg: { span: props.wrapperCol || 24 } }}
      colon={false}
      labelAlign={props.labelAlign || "left"}
    >
      <CheckboxGroup {...field} {...props} onChange={onChange}>
        {options.map((option) => (
          <Checkbox key={option.value} value={option.value}>
            {option.name}
          </Checkbox>
        ))}
      </CheckboxGroup>
    </AntFieldStyle>
  );
};

export const AntSelect = AntField(Select);
export const AntDatePicker = AntField(DatePicker);
export const AntInput = AntField(Input);
export const AntInputNumber = AntField(InputNumber);
export const AntTimePicker = AntField(TimePicker);
export const AntTextArea = AntField(TextArea);
export const AntPhone = AntField(CountryPhoneInput);
