import { React } from 'react';
import { Typography, Radio, Space, Modal } from 'antd';
// import { PlusOutlined } from '@ant-design/icons';
import styled from 'styled-components';

const { Text } = Typography;

const RadioWrapperStyle = styled.div`
    .ant-radio-wrapper {
        align-items: center;
        font-size: 16px;
    }
    .ant-radio-wrapper-checked{
        color:#1890ff;
    }
`
const ButtonWrapperStyle = styled.div`
    min-width: 48px;
    min-height:48px;
    background: #e4e6eb;
    padding-right: 0;
    padding-left: 0;
    padding-top: 8px;
    text-align: center;
    border-radius: 50%;
`

export const PrivacyModal = (props) => {
    const { visible, value, setValue, handleClose} = props;

    const onChange = e => {
        console.log('radio checked', e.target.value);
        setValue(e.target.value);
        handleClose();
    };

    const handleOk = e => {
        console.log(e);
        handleClose();
    };

    const handleCancel = e => {
        console.log(e);
        handleClose();
    };

    return (
        <Modal
            title="Select Privacy"
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            footer={null}
        >
            <RadioWrapperStyle>
                <Radio.Group onChange={onChange} value={value}>
                    <Space direction="vertical" size={20}>
                        <Radio value={1}>
                            <Space align="center">
                                <ButtonWrapperStyle shape="circle" ><i class="fa fa-globe fa-2x" aria-hidden="true" /></ButtonWrapperStyle>
                                <Text>Share to everyone</Text>
                            </Space>
                        </Radio>
                        <Radio value={2}>
                            <Space>
                            <ButtonWrapperStyle shape="circle" ><i class="fa fa-lock fa-2x" aria-hidden="true" /></ButtonWrapperStyle>
                                <Text>Only me</Text>
                            </Space>
                        </Radio>
                    </Space>
                </Radio.Group>
            </RadioWrapperStyle>

        </Modal>

    )
};

export default PrivacyModal;