export const CustomCheckBox = styled(Checkbox)`
  .delLogo {
    height: 15px;
  }
  .ant-checkbox {
    //margin-left: -20px;
    width: 0px;
  }

  .ant-checkbox-inner {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: none;
    left: 0px;
  }
  .ant-checkbox + span {
    padding-right: 0px;
    padding-left: 0px;
  }
  .ant-checkbox-checked .ant-checkbox-inner {
     background-color: #FF5252; 
     border-color: #FF5252;
  }
  .ant-checkbox-checked::after {
    border: none;
  }

  .ant-checkbox-checked .ant-checkbox-inner::after {
    position: inherit;
    display: block;
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0; 
    transform: rotate(45deg) scale(1.5) translate(-30%, -50%);
  }
`

