/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, useRef } from 'react';
import { Tag, Input} from 'antd';
import { PlusOutlined, TagsTwoTone } from '@ant-design/icons';
import { TweenOneGroup } from 'rc-tween-one'


export const EditableTagGroup = (props) => {
    const { selectedItems, setselectedItems, setEnableEdit } = props;
    const [init, setInit] = useState({
        tags: selectedItems || [],// ['Tag 1', 'Tag 2', 'Tag 3'],
        inputVisible: false,
        inputValue: '',
    });

    //console.log('init', tagList, init);

    const { tags, inputVisible, inputValue } = init;
    const inputRef = useRef();

    useEffect(() => {
        if (inputRef.current && inputRef.current.focus) {
            inputRef.current.focus();
        }
    }, []);

    const handleClose = removedTag => {
        const tags = init.tags.filter(tag => tag !== removedTag);
        console.log(tags);
        setselectedItems(tags);
        if (setEnableEdit) {
            setEnableEdit(true);
        }
        setInit(prev => ({
            ...prev,
            tags: tags
        }));
    };

    const showInput = () => {
        setInit(prev => ({
            ...prev,
            inputVisible: true
        }));
    };

    const handleInputChange = e => {
        setInit(prev => ({
            ...prev,
            inputValue: e.target.value
        }));
    };

    const handleInputConfirm = () => {
        const { inputValue } = init;
        let { tags } = init;
        if (inputValue && tags.indexOf(inputValue) === -1) {
            tags = [...tags, inputValue];
        }
        console.log(tags);
        setselectedItems(tags);
        if (setEnableEdit) {
            setEnableEdit(true);
        }
        setInit(prev => ({
            ...prev,
            tags: tags,
            inputVisible: false,
            inputValue: '',
        }));
    };

    const forMap = tag => {
        const tagElem = (
            <Tag
                closable
                onClose={e => {
                    e.preventDefault();
                    handleClose(tag);
                }}
            >
                {tag}
            </Tag>
        );
        return (
            <span key={tag} style={{ display: 'inline-block' }}>
                {tagElem}
            </span>
        );
    };

    const tagChild = tags.map(forMap);

    return (
        <>
            {/* <span style={{ marginRight: 5 }}><strong>Tags:</strong></span> */}
            <TagsTwoTone style={{paddingRight:5}}/>
            <div style={{ marginBottom: 16 }}>
                <TweenOneGroup
                    enter={{
                        scale: 0.8,
                        opacity: 0,
                        type: 'from',
                        duration: 100,
                        onComplete: e => {
                            e.target.style = '';
                        },
                    }}
                    leave={{ opacity: 0, width: 0, scale: 0, duration: 200 }}
                    appear={false}
                >
                    {tagChild}
                </TweenOneGroup>
            </div>
            {inputVisible && (
                <Input
                    ref={inputRef}
                    type="text"
                    size="small"
                    style={{ width: 78 }}
                    value={inputValue}
                    onChange={handleInputChange}
                    onBlur={handleInputConfirm}
                    onPressEnter={handleInputConfirm}
                />
            )}
            {!inputVisible && (
                <Tag onClick={showInput} className="site-tag-plus">
                    <PlusOutlined /> New Tag
                </Tag>
            )}
        </>
    );
}

export default EditableTagGroup;