import React, { useContext, useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import { Table, Input, Button, Popconfirm, Form, Select, InputNumber } from 'antd';

const TableStyle = styled.div`
    .ant-table .ant-table-expanded-row-fixed {
        padding: 0;
    }
    .ant-empty-normal {
        margin: 0;
    }
    .ant-empty-normal .ant-empty-image {
        height: 20px;
    }
`
const EditableContext = React.createContext(null);
const { Option } = Select;

const EditableRow = ({ index, ...props }) => {
    const [form] = Form.useForm();
    return (
        <Form form={form} component={false}>
            <EditableContext.Provider value={form}>
                <tr {...props} />
            </EditableContext.Provider>
        </Form>
    );
};

const EditableCell = ({
    title,
    editable,
    children,
    dataIndex,
    record,
    handleSave,
    ...restProps
}) => {
    const [editing, setEditing] = useState(false);
    const inputRef = useRef(null);
    const form = useContext(EditableContext);
    useEffect(() => {
        if (editing) {
            inputRef.current.focus();
        }
    }, [editing]);

    const toggleEdit = () => {
        setEditing(!editing);
        form.setFieldsValue({
            [dataIndex]: record[dataIndex],
        });
    };

    const save = async () => {
        try {
            const values = await form.validateFields();
            toggleEdit();
            handleSave({ ...record, ...values });
        } catch (errInfo) {
            console.log('Save failed:', errInfo);
        }
    };

    let childNode = children;

    if (editable) {
        childNode = editing ? (
            <Form.Item
                style={{
                    margin: 0,
                }}
                name={dataIndex}
                rules={[
                    {
                        required: true,
                        message: `${title} is required.`,
                    },
                ]}
            >
                <Input ref={inputRef} onPressEnter={save} onBlur={save} />
            </Form.Item>
        ) : (
            <div
                className="editable-cell-value-wrap"
                style={{
                    paddingRight: 24,
                }}
                onClick={toggleEdit}
            >
                {children}
            </div>
        );
    }

    return <td {...restProps}>{childNode}</td>;
};

export const EditTable = (props) => {

    const { data, setData } = props;
   
    const columns = [
        {
            title: 'Tasks',
            dataIndex: 'Task',
            width: '50%',
            editable: true,
        },
        {
            title: 'Hours',
            dataIndex: 'Hours',
            render: (_, record) =>
                data.dataSource.length >= 1 ? (

                    <InputNumber min={1} max={8}
                        defaultValue={record.Hours}
                        value={record.Hours}
                        onChange={e => handleSaveHours(record, e)}
                    />
                ) : null,
        },
        {
            title: 'Types',
            dataIndex: 'Type',
            render: (_, record) =>
                data.dataSource.length >= 1 ? (
                    <Select
                        defaultValue={record.Type}
                        value={record.Type}
                        onChange={e => handleSaveSelectOption(record, e)}
                    >
                        <Option value="Support">Support</Option>
                        <Option value="Project">Project</Option>
                        <Option value="Training">Training</Option>
                        <Option value="Leave">Leave</Option>
                    </Select>
                ) : null,
        },
        {
            title: 'Delete',
            dataIndex: 'Delete',
            render: (_, record) =>
                data.dataSource.length >= 1 ? (
                    <Popconfirm title="Sure to delete?" onConfirm={() => handleDelete(record.key)}>
                        <a>Delete</a>
                    </Popconfirm>
                ) : null,
        },
    ];

    const handleDelete = (key) => {
        const { count, dataSource } = data;
        setData(prev => ({
            ...prev,
            dataSource: dataSource.filter((item) => item.key !== key),
            count: count - 1,
        }))
    };

    const handleAdd = () => {
        const { count, dataSource } = data;
        const newData = {
            key: count,
            Task: '_',
            Hours: 1,
            Type: 'Project',
        };

        setData(prev => ({
            ...prev,
            dataSource: [...dataSource, newData],
            count: count + 1,
        }))
    };

    useEffect(() => {
        // console.log('dataSource', data);
    }, [data])

    const handleSaveHours = (row, value) => {
        const newData = [...data.dataSource];
        const index = newData.findIndex((item) => row.key === item.key);
        let item = newData[index];
        item.Hours = value;
        newData.splice(index, 1, { ...item, ...row });
        setData(prev => ({
            ...prev,
            dataSource: newData,
        }))
    }

    const handleSaveSelectOption = (row, value) => {
        const newData = [...data.dataSource];
        const index = newData.findIndex((item) => row.key === item.key);
        let item = newData[index];
        item.Type = value;
        newData.splice(index, 1, { ...item, ...row });
        setData(prev => ({
            ...prev,
            dataSource: newData,
        }))
    };

    const handleSave = (row) => {
        const newData = [...data.dataSource];
        const index = newData.findIndex((item) => row.key === item.key);
        const item = newData[index];
        newData.splice(index, 1, { ...item, ...row });
        setData(prev => ({
            ...prev,
            dataSource: newData,
        }))
    };

    const components = {
        body: {
            row: EditableRow,
            cell: EditableCell,
        },
    };

    const columns_ = columns.map((col) => {
        // if (!col.editable) {
        //     return col;
        // }

        return {
            ...col,
            onCell: (record) => ({
                record,
                editable: col.editable,
                dataIndex: col.dataIndex,
                title: col.title,
                handleSave: handleSave,
            }),
        };
    });

    return (
        <TableStyle>
            <Button
                onClick={handleAdd}
                type="primary"
                style={{
                    marginBottom: 2,
                }}
            >
                Add a row
            </Button>

            <Table
                components={components}
                rowClassName={() => 'editable-row'}
                bordered
                dataSource={data.dataSource}
                columns={columns_}
                size="small"
                pagination={false}
                scroll={{ x: 1000, y: 150 }}
            />
        </TableStyle>
    );
}

export default EditTable;